footer {
  padding: 0px 10px;
  box-shadow: 0px -2px 8px 0px #c1c1c1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  flex-shrink: 0;
}
a {
  text-decoration: none;
  color: black;
  margin: 0px 5px;
}

.footer .item {
  cursor: pointer;
}
@media (max-width: 420px) {
  div.name__dev {
    display: flex;
    flex-direction: column;
  }
}
